$color-primary: #2c3e50;
$color-primary-dark: darken($color-primary, 20%);
$color-secondary: #ecf0f1;
$color-white: #fff;
$color-black: #000;
$color-shadow: rgba(0, 0, 0, 0.3);
$color-hover-menu: rgba(0, 0, 0, 0.1);

$spacing-substep: 20px;
$border-radius: 8px;
$layout-spacing: 30px;
$drop-border-radius: 4px;

$z-index-0: 0;
$z-index-1: 100;
$z-index-2: 200;
$z-index-3: 300;
$z-index-4: 400;
$z-index-5: 500;
$z-index-6: 600;
$z-index-7: 700;
$z-index-8: 800;
$z-index-9: 900;
$z-index-10: 1000;
